@import '../../mixings.scss';

.nav-bar{
    background-color: $secondary-color;
    height: 56px;
    justify-content: center; 
  
    .search-block{
        padding-top:9px;
        padding-bottom:9px;
        display: block;
        margin-left: auto;
        margin-right: auto;
        width:80%;
        display: flex;

        .logo{
            width: 54px;
            margin-right: 25px;
        }

        form{
            width:100%;
            display: flex;

            .search-input{
                flex:1;         
                border-radius: $corner-radius 0 0 $corner-radius;
                border-style: none;
                padding-left: $space;
                font-size: $font-size-mid;
            }
    
            .search-input:focus, .button-search:focus {
                outline: none;
            }
    
            .button-search{
                width:40px;
                border-style: none;
                border-radius: 0 $corner-radius $corner-radius 0;
                background-color: $gray-lighter;
            }
        }
    }
}